.ButtonsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 70px;
  background-color: #101315;
  gap: 10px;

  @media screen and (max-width: 1100px) {
    flex-direction: row;
    height: 70px;
  }

  @media screen and (min-width: 1100px) {
    flex-direction: column;
    width: 70px;
  }
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background-color: #17212d;
  color: #2295ff;

  @media screen and (max-width: 1100px) {
    flex-direction: row;
  }

  @media screen and (min-width: 1100px) {
    flex-direction: column;
  }

  &:hover{
    background-color: #193450;
  }
}
